<template>
  <div class="canvas">
    <div class=""></div>
    <canvas id="canvas"></canvas>
  </div>
</template>
<script setup>
import { onMounted } from "vue";
onMounted(() => {
  drawCanvas();
});
const drawCanvas = () => {
  const canvas = document.getElementById("canvas");
  const ctx = canvas.getContext("2d");
  canvas.width = 400;
  canvas.height = 400;
  ctx.beginPath();
  ctx.arc(200, 200, 200, 0, 2 * Math.PI);
  ctx.strokeStyle = "green";
  ctx.moveTo(200, 400);
  ctx.lineTo(300, 300); // 连接到 x2, y2
  // ...                                          // 更多的点连接
  //   ctx.closePath(); // 关闭路径
  //   ctx.fill();
  ctx.stroke();
};
</script>
<style lang="scss" scoped>
.canvas {
  width: 100%;
  height: 100%;
  padding: 50px 0;
  //   #canvas {
  //     width: 500px;
  //     height: 500px;
  //   }
}
</style>